F<template>
  <div>
    <v-card tile elevation="0" color="#F5F5F5">
      <v-container grid-list-xs fill-height fluid>
        <v-row class="">
          <v-col cols="12">
            <v-row justify="center" align="center" class="pb-5 pt-12"
              ><v-img max-width="190" src="../assets/inetlogo.png"></v-img
            ></v-row>
            <v-row justify="center" align="center">
              <div class="titletxt txtbold">ATK Screening Box</div>
            </v-row>
            <v-row justify="center" align="center">
              <div class="titletxt txtbold">Setting</div>
            </v-row>
          </v-col>
        </v-row>

        <v-container grid-list-xs fluid class="pt-15 mb-16">
          <v-card color="white" class="pt-1 pb-10 px-3" elevation="10">
            <v-row justify="center" class="pt-7">
              <v-col cols="111" class="pb-0 mb-0">
                <v-text-field
                  dense
                  outlined
                  label="Serial Number"
                  v-model="sn"
                ></v-text-field>
              </v-col>
            </v-row>

            <v-row justify="center" class="mt-n1">
              <v-col cols="10">
                <v-row justify="center" class="txtsmall">
                  กรอก Serial Number ที่อยู่บนอุปกรณ์ของท่าน</v-row
                >
                <v-row justify="center">
                  <div class="txtsmall">และกดปุ่ม</div>
                  <div class="registtxt txtbold">"ลงทะเบียน"</div>
                </v-row>
                <v-row justify="center" class="txtsmall">
                  เพื่อดำเนินการลงทะเบียนอุปกรณ์
                </v-row>
              </v-col>
            </v-row>

            <v-row justify="center" class="pt-10">
              <v-col cols="11">
                <v-row justify="center">
                  <v-btn
                    :disabled="!btn"
                    elevation="0"
                    block
                    color="primary"
                    @click="connect"
                    class="secondary--text btntxt txtbold"
                    >ลงทะเบียน</v-btn
                  >
                </v-row>
              </v-col>
            </v-row>
          </v-card>
        </v-container>
      </v-container>
    </v-card>

    <v-dialog v-model="notiCard" transition="dialog-bottom-transition">
      <v-card color="white" class="text-center" height="200" v-if="!err_handle">
        <v-row class="mx-0 px-0">
          <v-col class="py-0 ma-0">
            <sweetalert-icon icon="success"></sweetalert-icon>
          </v-col>
        </v-row>
        <v-row class="mx-0 px-0">
          <v-col class="pb-0">
            <div style="font-size: 20px; font-weight: bold">
              ลงทะเบียนสำเร็จ
            </div>
          </v-col>
        </v-row>
      </v-card>

      <v-card color="white" class="text-center" height="200" v-else>
        <v-row class="mx-0 px-0">
          <v-col class="py-0 ma-0">
            <sweetalert-icon icon="error"></sweetalert-icon>
          </v-col>
        </v-row>
        <v-row class="mx-0 px-0">
          <v-col class="pb-0">
            <div class="txtreg" v-if="err_msg === 0">
              เกิดข้อผิดพลาด กรุณาลองใหม่อีกครั้ง หรือติดต่อเจ้าหน้าที่
            </div>
            <div class="txtreg" v-else-if="err_msg === 1">
              ไม่พบอุปกรณ์ของท่าน โปรดตรวจสอบ Serial Number อีกครั้ง
            </div>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>
    <loader></loader>
  </div>
</template>

<script>
import loader from "@/components/loader";
import SweetalertIcon from "vue-sweetalert-icons";
export default {
  components: {
    loader,
    SweetalertIcon,
  },
  data() {
    return {
      whenshow: "beforescan",
      connect_info: "",
      notiCard: false,
      err_handle: false,
      err_msg: 0,
      sn: "",
      oneID: null,
      id_Arr: [],
      btn_status: false,
    };
  },
  async mounted() {
    var paramsString = window.location.href;
    var url = new URL(paramsString);
    const one_token = url.searchParams.get("onechat_token");
    localStorage.setItem("token", one_token);
    await this.get_onedata();
  },
  computed: {
    btn() {
      return this.btn_status;
    },
  },
  watch: {
    sn(newValue) {
      if (newValue.length >= 10) {
        this.btn_status = true;
      } else {
        this.btn_status = false;
      }
    },
  },
  methods: {
    async get_onedata() {
      const body = {
        source: localStorage.getItem("token"),
      };
      const headers = {
        Authorization:
          "Bearer A4c106a02df725292bdd6559178ca5e88d155e516115240368068c3ffd2149e48d2cb338c47a54d708bbc7536415a4fd1",
      };

      this.axios
        .post(
          "https://chat-api.one.th/manage/api/v1/login_onechat_token",
          body,
          { headers }
        )
        .then((result) => {
          this.oneID = result.data.account.profile.one_id;
        });
    },
    async regist(id) {
      // this.oneID = "82675349452898043";
      const url = "https://uat-atkscreening.one.th/gov/api/v1/dashboard/user";
      const body = {
        user_id: this.oneID,
        serial_number: this.sn,
      };
      await this.axios
        .put(url, body, { params: { id: id } })
        .then((result) => {
          if (result.data.status === "success") {
            console.log("SUCCESS !!");
          }
        })
        .catch((err) => {
          console.log(err);
          this.err_handle = true;
        });
    },
    async get_merchant() {
      const url = "https://uat-atkscreening.one.th/gov/api/v1/dashboard/user";
      // this.oneID = "82675349452898043";
      await this.axios
        .get(url, { params: { user_id: this.oneID } })
        .then(async (result) => {
          for (const data of result.data.data) {
            if (!this.id_Arr.includes(data._id)) {
              this.id_Arr.push(data._id);
            }
          }
          for (const id of this.id_Arr) {
            await this.regist(id);
          }
        })
        .catch((err) => {
          console.log(err);
          this.err_handle = true;
        });
    },
    async connect() {
      this.$store.commit("loading", true);
      const url = "http://203.151.164.229:8081/api/v1/atk/devices";
      // const url = "service.php";
      const headers = {
        Authorization:
          "$2a$12$PpO0HqBajBz9YSAiSG.eOutmsIPC45nEm4VsXGCnq4JmqZ4BNUZSy",
      };
      await this.axios
        .get(url + "/" + this.sn, { headers })
        .then(async (result) => {
          if (result.data.type) {
            await this.get_merchant();
            this.$store.commit("loading", false);
          } else {
            this.err_handle = true;
            this.err_msg = 1; //ไม่พบอุปกรณ์ โปรดตรวจสอบ Serial number อีกครั้ง
            this.$store.commit("loading", false);
          }
        })
        .catch((err) => {
          console.log(err);
          this.err_handle = true;
        });

      this.notiCard = true;
      setTimeout(() => {
        this.notiCard = false;
        this.err_handle = false;
        this.err_msg = 0;
      }, 2000);

      // await this.axios
      //   .get(
      //     url,
      //     {
      //       sn: this.sn,
      //     },
      //     { headers }
      //   )
      //   .then((result) => {
      //     console.log(result);
      //     // if (result.data.type) {
      //     //   this.get_merchant();
      //     // }
      //   })
      //   .catch((err) => {
      //     console.log(err);
      //   });
    },
  },
};
</script>

